<!-- 折线 -->

<template>
  <div class="demo">
    <div class="box">
      <h3>基础子弹图-水平方向</h3>
      <div id="container"></div>
    </div>
    <div class="box">
      <h3>分组子弹图</h3>
      <div id="containerG2"></div>
    </div>
  </div>
</template>

<script>
import { Bullet } from '@antv/g2plot'

export default {
  components: {},
  data() {
    return {}
  },
  mounted() {
    // 在 mounted 中构建，防止created 中无法获取标签元素
    const data = [
      {
        title: '满意度',
        ranges: [100],
        measures: [80],
        target: 85
      }
    ]

    const bulletPlot = new Bullet('container', {
      data,
      measureField: 'measures',
      rangeField: 'ranges',
      targetField: 'target',
      xField: 'title',
      color: {
        range: '#f0efff',
        measure: '#5B8FF9',
        target: '#3D76DD'
      },
      xAxis: {
        line: null
      },
      yAxis: false,
      // 自定义 legend
      legend: {
        custom: true,
        position: 'bottom',
        items: [
          {
            value: '实际值',
            name: '实际值',
            marker: { symbol: 'square', style: { fill: '#5B8FF9', r: 5 } }
          },
          {
            value: '目标值',
            name: '目标值',
            marker: { symbol: 'line', style: { stroke: '#3D76DD', r: 5 } }
          }
        ]
      }
    })

    bulletPlot.render()

    this.pointFun()
  },
  methods: {
    pointFun() {
      const data = [
        {
          title: '重庆',
          ranges: [30, 90, 120],
          measures: [65],
          target: 80
        },
        {
          title: '杭州',
          ranges: [30, 90, 120],
          measures: [50],
          target: 100
        },
        {
          title: '广州',
          ranges: [30, 90, 120],
          measures: [40],
          target: 85
        },
        {
          title: '深圳',
          ranges: [30, 90, 120],
          measures: [50],
          target: 100
        }
      ]

      const bulletPlot = new Bullet('containerG2', {
        data,
        measureField: 'measures',
        rangeField: 'ranges',
        targetField: 'target',
        xField: 'title',
        color: {
          range: ['#FFbcb8', '#FFe0b0', '#bfeec8'],
          measure: '#5B8FF9',
          target: '#39a3f4'
        },
        label: {
          measure: {
            position: 'middle',
            style: {
              fill: '#fff'
            }
          }
        },
        xAxis: {
          line: null
        },
        yAxis: false,
        // 自定义 legend
        legend: {
          custom: true,
          position: 'bottom',
          items: [
            {
              value: '差',
              name: '差',
              marker: { symbol: 'square', style: { fill: '#FFbcb8', r: 5 } }
            },
            {
              value: '良',
              name: '良',
              marker: { symbol: 'square', style: { fill: '#FFe0b0', r: 5 } }
            },
            {
              value: '优',
              name: '优',
              marker: { symbol: 'square', style: { fill: '#bfeec8', r: 5 } }
            },
            {
              value: '实际值',
              name: '实际值',
              marker: { symbol: 'square', style: { fill: '#5B8FF9', r: 5 } }
            },
            {
              value: '目标值',
              name: '目标值',
              marker: { symbol: 'line', style: { stroke: '#39a3f4', r: 5 } }
            }
          ]
        }
      })

      bulletPlot.render()
    }
  },
  created() {
    // var wrapperElement = '<div style=position:relative;></div>'
    // console.log(document.getElementById('container'))
    // document.getElementById('container').appendChild(wrapperElement)
    // setTimeout(() => {
    // }, 2000)
  }
}
</script>

<style lang="stylus" scoped>
.demo {
  display: flex;
  flex-flow: wrap;

  .box {
    box-shadow: 0px 2px 25px 0px rgba(133, 153, 181, 0.2);
    border-radius: 10px;
    display: inline-block;
    padding: 0 20px 20px;
    margin: 20px;

    h3 {
      line-height: 50px;
      height: 50px;
      text-align: center;
    }
  }

  #container {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerG2 {
    display: block;
    width: 500px;
    height: 300px;
  }
}
</style>



